"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { DocumentUpload } from "@/components/DocumentUpload";
import { DocumentTable } from "@/components/DocumentTable";
import { VersionHistoryModal } from "@/components/VersionHistoryModal";
import { ApprovalModal } from "@/components/ApprovalModal";
import { ApprovalHistory } from "@/components/ApprovalHistory";

export default function Documents() {
  const [selectedDocument, setSelectedDocument] = useState<any>(null);
  const [showVersionHistory, setShowVersionHistory] = useState(false);
  const [showApprovalModal, setShowApprovalModal] = useState(false);
  const [showApprovalHistory, setShowApprovalHistory] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");

  // Usuario actual (en una aplicaci√≥n real vendr√≠a del contexto de autenticaci√≥n)
  const currentUser = {
    name: "Juan P√©rez",
    role: "Administrador" // Puede ser: "Administrador", "Editor", "Visualizador"
  };

  // Datos de documentos de ejemplo con estados de aprobaci√≥n
  const [documents, setDocuments] = useState([
    {
      id: 1,
      name: "Reporte_Financiero_Q4.pdf",
      version: "v2.1",
      lastModified: "2024-01-15",
      owner: "Sarah Chen",
      size: "2.4 MB",
      type: "PDF",
      status: "pendiente" as const,
    },
    {
      id: 2,
      name: "Estrategia_Marketing.docx",
      version: "v1.3",
      lastModified: "2024-01-14",
      owner: "Mike Johnson",
      size: "1.8 MB",
      type: "Word",
      status: "aprobado" as const,
    },
    {
      id: 3,
      name: "Cronograma_Proyecto.xlsx",
      version: "v3.0",
      lastModified: "2024-01-13",
      owner: "Emily Davis",
      size: "856 KB",
      type: "Excel",
      status: "rechazado" as const,
    },
    {
      id: 4,
      name: "Mockups_Dise√±o.psd",
      version: "v1.0",
      lastModified: "2024-01-12",
      owner: "Alex Wilson",
      size: "15.2 MB",
      type: "Photoshop",
      status: "pendiente" as const,
    },
  ]);

  // Historial de aprobaciones de ejemplo
  const [approvalHistory, setApprovalHistory] = useState([
    {
      id: 1,
      documentId: 2,
      version: "v1.3",
      action: "aprobado" as const,
      approver: "Juan P√©rez",
      approverRole: "Administrador",
      date: "2024-01-14",
      time: "10:30",
      comments: "Documento aprobado. Excelente trabajo en la estrategia de marketing.",
      signature: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mNkYPhfDwAChwGA60e6kgAAAABJRU5ErkJggg=="
    },
    {
      id: 2,
      documentId: 3,
      version: "v3.0",
      action: "rechazado" as const,
      approver: "Mar√≠a Garc√≠a",
      approverRole: "Editor",
      date: "2024-01-13",
      time: "16:45",
      comments: "El cronograma necesita revisi√≥n. Las fechas de entrega no son realistas para el alcance del proyecto.",
      signature: "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAADUlEQVR42mNkYPhfDwAChwGA60e6kgAAAABJRU5ErkJggg=="
    }
  ]);

  const filteredDocuments = documents.filter(doc =>
    doc.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    doc.owner.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const handleViewVersionHistory = (document: any) => {
    setSelectedDocument(document);
    setShowVersionHistory(true);
  };

  const handleApprovalAction = (document: any) => {
    setSelectedDocument(document);
    setShowApprovalModal(true);
  };

  const handleViewApprovalHistory = (document: any) => {
    setSelectedDocument(document);
    setShowApprovalHistory(true);
  };

  const handleApprove = async (documentId: number, signature: string, comments: string) => {
    // Actualizar el estado del documento
    setDocuments(prev => prev.map(doc => 
      doc.id === documentId 
        ? { ...doc, status: "aprobado" as const }
        : doc
    ));

    // Agregar al historial de aprobaciones
    const newApproval = {
      id: approvalHistory.length + 1,
      documentId,
      version: selectedDocument?.version || "v1.0",
      action: "aprobado" as const,
      approver: currentUser.name,
      approverRole: currentUser.role,
      date: new Date().toISOString().split('T')[0],
      time: new Date().toTimeString().split(' ')[0].substring(0, 5),
      comments,
      signature
    };

    setApprovalHistory(prev => [...prev, newApproval]);
    
    console.log(`Documento ${documentId} aprobado por ${currentUser.name}`);
  };

  const handleReject = async (documentId: number, signature: string, comments: string) => {
    // Actualizar el estado del documento
    setDocuments(prev => prev.map(doc => 
      doc.id === documentId 
        ? { ...doc, status: "rechazado" as const }
        : doc
    ));

    // Agregar al historial de aprobaciones
    const newApproval = {
      id: approvalHistory.length + 1,
      documentId,
      version: selectedDocument?.version || "v1.0",
      action: "rechazado" as const,
      approver: currentUser.name,
      approverRole: currentUser.role,
      date: new Date().toISOString().split('T')[0],
      time: new Date().toTimeString().split(' ')[0].substring(0, 5),
      comments,
      signature
    };

    setApprovalHistory(prev => [...prev, newApproval]);

    // En una aplicaci√≥n real, aqu√≠ se enviar√≠a una notificaci√≥n al propietario del documento
    console.log(`Documento ${documentId} rechazado por ${currentUser.name}. Notificando al propietario.`);
    
    // Simular notificaci√≥n al propietario
    const document = documents.find(doc => doc.id === documentId);
    if (document) {
      alert(`Notificaci√≥n enviada a ${document.owner}: Su documento "${document.name}" ha sido rechazado y requiere revisi√≥n.`);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Documentos</h1>
          <p className="text-gray-600 mt-2">Gestiona tus documentos y control de versiones</p>
        </div>
        <Button className="bg-gray-900 hover:bg-gray-800">
          Nueva Carpeta
        </Button>
      </div>

      {/* Secci√≥n de Subida */}
      <Card>
        <CardHeader>
          <CardTitle>Subir Documentos</CardTitle>
        </CardHeader>
        <CardContent>
          <DocumentUpload />
        </CardContent>
      </Card>

      {/* B√∫squeda y Filtros */}
      <Card>
        <CardHeader>
          <CardTitle>Biblioteca de Documentos</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center gap-4 mb-6">
            <div className="flex-1">
              <Input
                placeholder="Buscar documentos..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="max-w-sm"
              />
            </div>
            <div className="flex items-center gap-2">
              <Button variant="outline" size="sm">
                Filtrar
              </Button>
              <Button variant="outline" size="sm">
                Ordenar
              </Button>
            </div>
          </div>

          <DocumentTable
            documents={filteredDocuments}
            onViewVersionHistory={handleViewVersionHistory}
            onApprovalAction={handleApprovalAction}
            onViewApprovalHistory={handleViewApprovalHistory}
            currentUserRole={currentUser.role}
          />
        </CardContent>
      </Card>

      {/* Modal de Historial de Versiones */}
      <VersionHistoryModal
        document={selectedDocument}
        open={showVersionHistory}
        onOpenChange={setShowVersionHistory}
      />

      {/* Modal de Aprobaci√≥n */}
      <ApprovalModal
        document={selectedDocument}
        open={showApprovalModal}
        onOpenChange={setShowApprovalModal}
        onApprove={handleApprove}
        onReject={handleReject}
        userRole={currentUser.role}
      />

      {/* Historial de Aprobaciones */}
      {showApprovalHistory && selectedDocument && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-hidden">
            <div className="p-6 border-b">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-semibold">Historial de Aprobaciones - {selectedDocument.name}</h2>
                <Button variant="outline" onClick={() => setShowApprovalHistory(false)}>
                  Cerrar
                </Button>
              </div>
            </div>
            <div className="p-6 overflow-auto max-h-[calc(90vh-120px)]">
              <ApprovalHistory
                documentId={selectedDocument.id}
                approvals={approvalHistory}
              />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
