"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

interface ApprovalRecord {
  id: number;
  documentId: number;
  version: string;
  action: 'aprobado' | 'rechazado' | 'pendiente';
  approver: string;
  approverRole: string;
  date: string;
  time: string;
  comments: string;
  signature: string;
}

interface ApprovalHistoryProps {
  documentId: number;
  approvals: ApprovalRecord[];
}

export function ApprovalHistory({ documentId, approvals }: ApprovalHistoryProps) {
  const documentApprovals = approvals.filter(approval => approval.documentId === documentId);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'aprobado':
        return 'bg-green-100 text-green-800';
      case 'rechazado':
        return 'bg-red-100 text-red-800';
      case 'pendiente':
        return 'bg-yellow-100 text-yellow-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'aprobado':
        return '‚úÖ';
      case 'rechazado':
        return '‚ùå';
      case 'pendiente':
        return '‚è≥';
      default:
        return 'üìã';
    }
  };

  const formatDateTime = (date: string, time: string) => {
    const dateObj = new Date(`${date}T${time}`);
    return dateObj.toLocaleDateString('es-ES', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const getAuthorInitials = (name: string) => {
    return name.split(' ').map((n: string) => n[0]).join('');
  };

  if (documentApprovals.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="text-lg">Historial de Aprobaciones</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="text-center py-8 text-gray-500">
            <div className="text-4xl mb-2">üìã</div>
            <p className="text-lg font-medium">Sin Historial de Aprobaciones</p>
            <p className="text-sm">Este documento a√∫n no ha pasado por el proceso de aprobaci√≥n</p>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="text-lg">Historial de Aprobaciones</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {documentApprovals.map((approval, index) => (
            <div key={approval.id} className="border rounded-lg p-4 bg-gray-50">
              <div className="flex items-start justify-between mb-3">
                <div className="flex items-center gap-3">
                  <div className="text-2xl">{getStatusIcon(approval.action)}</div>
                  <div>
                    <div className="flex items-center gap-2">
                      <Badge className={getStatusColor(approval.action)}>
                        {approval.action.charAt(0).toUpperCase() + approval.action.slice(1)}
                      </Badge>
                      <span className="text-sm text-gray-500">Versi√≥n {approval.version}</span>
                    </div>
                    <p className="text-sm text-gray-600 mt-1">
                      {formatDateTime(approval.date, approval.time)}
                    </p>
                  </div>
                </div>
                
                <div className="flex items-center gap-2">
                  <div className="h-8 w-8 rounded-full bg-gray-300 flex items-center justify-center">
                    <span className="text-xs font-medium text-gray-700">
                      {getAuthorInitials(approval.approver)}
                    </span>
                  </div>
                  <div className="text-right">
                    <p className="text-sm font-medium text-gray-900">{approval.approver}</p>
                    <p className="text-xs text-gray-500">{approval.approverRole}</p>
                  </div>
                </div>
              </div>

              {approval.comments && (
                <div className="mb-3">
                  <p className="text-sm font-medium text-gray-700 mb-1">Comentarios:</p>
                  <p className="text-sm text-gray-600 bg-white p-2 rounded border">
                    {approval.comments}
                  </p>
                </div>
              )}

              {approval.signature && (
                <div className="border-t pt-3">
                  <p className="text-sm font-medium text-gray-700 mb-2">Firma Digital:</p>
                  <div className="bg-white border rounded p-2 max-w-xs">
                    <img 
                      src={approval.signature} 
                      alt="Firma digital" 
                      className="max-h-16 w-auto"
                      onError={(e) => {
                        const target = e.currentTarget as HTMLImageElement;
                        target.style.display = 'none';
                        const fallback = target.nextElementSibling as HTMLElement;
                        if (fallback) fallback.style.display = 'block';
                      }}
                    />
                    <div style={{ display: 'none' }} className="text-xs text-gray-500 italic">
                      Firma digital verificada
                    </div>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </CardContent>
    </Card>
  );
}
