"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { SignatureCanvas } from "@/components/SignatureCanvas";

interface Document {
  id: number;
  name: string;
  version: string;
  lastModified: string;
  owner: string;
  size: string;
  type: string;
  status: 'pendiente' | 'aprobado' | 'rechazado';
}

interface ApprovalModalProps {
  document: Document | null;
  open: boolean;
  onOpenChange: (open: boolean) => void;
  onApprove: (documentId: number, signature: string, comments: string) => void;
  onReject: (documentId: number, signature: string, comments: string) => void;
  userRole: string;
}

export function ApprovalModal({ 
  document, 
  open, 
  onOpenChange, 
  onApprove, 
  onReject, 
  userRole 
}: ApprovalModalProps) {
  const [signature, setSignature] = useState("");
  const [comments, setComments] = useState("");
  const [isSubmitting, setIsSubmitting] = useState(false);

  if (!document) return null;

  const canApprove = userRole === 'Administrador' || userRole === 'Editor';

  const handleApprove = async () => {
    if (!signature.trim()) {
      alert("Por favor, proporcione su firma digital");
      return;
    }

    setIsSubmitting(true);
    try {
      await onApprove(document.id, signature, comments);
      setSignature("");
      setComments("");
      onOpenChange(false);
    } catch (error) {
      console.error("Error al aprobar documento:", error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleReject = async () => {
    if (!signature.trim()) {
      alert("Por favor, proporcione su firma digital");
      return;
    }

    if (!comments.trim()) {
      alert("Por favor, proporcione comentarios para el rechazo");
      return;
    }

    setIsSubmitting(true);
    try {
      await onReject(document.id, signature, comments);
      setSignature("");
      setComments("");
      onOpenChange(false);
    } catch (error) {
      console.error("Error al rechazar documento:", error);
    } finally {
      setIsSubmitting(false);
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'aprobado':
        return 'bg-green-100 text-green-800';
      case 'rechazado':
        return 'bg-red-100 text-red-800';
      case 'pendiente':
        return 'bg-yellow-100 text-yellow-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-2xl max-h-[90vh] overflow-hidden flex flex-col">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-3">
            <span className="text-lg">üìã</span>
            <div>
              <div className="text-lg font-semibold">Aprobaci√≥n de Documento</div>
              <div className="text-sm text-gray-500 font-normal">{document.name}</div>
            </div>
          </DialogTitle>
        </DialogHeader>
        
        <div className="flex-1 overflow-auto space-y-6">
          {/* Informaci√≥n del Documento */}
          <div className="bg-gray-50 p-4 rounded-lg">
            <h3 className="font-medium text-gray-900 mb-3">Informaci√≥n del Documento</h3>
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div>
                <span className="text-gray-500">Nombre:</span>
                <p className="font-medium">{document.name}</p>
              </div>
              <div>
                <span className="text-gray-500">Versi√≥n:</span>
                <p className="font-medium">{document.version}</p>
              </div>
              <div>
                <span className="text-gray-500">Propietario:</span>
                <p className="font-medium">{document.owner}</p>
              </div>
              <div>
                <span className="text-gray-500">Estado Actual:</span>
                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(document.status)}`}>
                  {document.status.charAt(0).toUpperCase() + document.status.slice(1)}
                </span>
              </div>
            </div>
          </div>

          {/* Firma Digital */}
          <div className="space-y-3">
            <Label htmlFor="signature">Firma Digital *</Label>
            <SignatureCanvas
              value={signature}
              onChange={setSignature}
              placeholder="Escriba su firma aqu√≠..."
            />
          </div>

          {/* Comentarios */}
          <div className="space-y-3">
            <Label htmlFor="comments">
              Comentarios {document.status === 'pendiente' ? '(Opcional)' : '*'}
            </Label>
            <Textarea
              id="comments"
              value={comments}
              onChange={(e) => setComments(e.target.value)}
              placeholder="Agregue sus comentarios sobre la aprobaci√≥n o rechazo..."
              rows={4}
            />
          </div>

          {!canApprove && (
            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4">
              <div className="flex items-center gap-2">
                <span className="text-yellow-600">‚ö†Ô∏è</span>
                <p className="text-sm text-yellow-800">
                  No tiene permisos suficientes para aprobar o rechazar documentos. 
                  Solo los usuarios con rol de Administrador o Editor pueden realizar esta acci√≥n.
                </p>
              </div>
            </div>
          )}
        </div>

        <div className="flex items-center justify-between pt-4 border-t">
          <Button variant="outline" onClick={() => onOpenChange(false)}>
            Cancelar
          </Button>
          
          {canApprove && (
            <div className="flex items-center gap-2">
              <Button
                variant="outline"
                onClick={handleReject}
                disabled={isSubmitting}
                className="text-red-600 hover:text-red-700 border-red-200 hover:border-red-300"
              >
                {isSubmitting ? "Procesando..." : "Rechazar"}
              </Button>
              <Button
                onClick={handleApprove}
                disabled={isSubmitting}
                className="bg-green-600 hover:bg-green-700"
              >
                {isSubmitting ? "Procesando..." : "Aprobar"}
              </Button>
            </div>
          )}
        </div>
      </DialogContent>
    </Dialog>
  );
}
