"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";

interface Document {
  id: number;
  name: string;
  version: string;
  lastModified: string;
  owner: string;
  size: string;
  type: string;
  status: 'pendiente' | 'aprobado' | 'rechazado';
}

interface DocumentTableProps {
  documents: Document[];
  onViewVersionHistory: (document: Document) => void;
  onApprovalAction: (document: Document) => void;
  onViewApprovalHistory: (document: Document) => void;
  currentUserRole: string;
}

type SortField = 'name' | 'lastModified' | 'owner' | 'size';
type SortDirection = 'asc' | 'desc';

export function DocumentTable({ 
  documents, 
  onViewVersionHistory, 
  onApprovalAction, 
  onViewApprovalHistory,
  currentUserRole 
}: DocumentTableProps) {
  const [sortField, setSortField] = useState<SortField>('lastModified');
  const [sortDirection, setSortDirection] = useState<SortDirection>('desc');

  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  const sortedDocuments = [...documents].sort((a, b) => {
    let aValue: string | number = a[sortField];
    let bValue: string | number = b[sortField];

    if (sortField === 'lastModified') {
      aValue = new Date(aValue).getTime();
      bValue = new Date(bValue).getTime();
    } else if (sortField === 'size') {
      aValue = parseFloat(aValue.replace(/[^\d.]/g, ''));
      bValue = parseFloat(bValue.replace(/[^\d.]/g, ''));
    } else {
      aValue = aValue.toString().toLowerCase();
      bValue = bValue.toString().toLowerCase();
    }

    if (sortDirection === 'asc') {
      return aValue < bValue ? -1 : aValue > bValue ? 1 : 0;
    } else {
      return aValue > bValue ? -1 : aValue < bValue ? 1 : 0;
    }
  });

  const getFileTypeIcon = (type: string) => {
    switch (type.toLowerCase()) {
      case 'pdf':
        return 'üìÑ';
      case 'word':
        return 'üìù';
      case 'excel':
        return 'üìä';
      case 'photoshop':
        return 'üé®';
      default:
        return 'üìÅ';
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'aprobado':
        return 'bg-green-100 text-green-800';
      case 'rechazado':
        return 'bg-red-100 text-red-800';
      case 'pendiente':
        return 'bg-yellow-100 text-yellow-800';
      default:
        return 'bg-gray-100 text-gray-800';
    }
  };

  const canApprove = currentUserRole === 'Administrador' || currentUserRole === 'Editor';

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('es-ES', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    });
  };

  const SortableHeader = ({ field, children }: { field: SortField; children: React.ReactNode }) => (
    <TableHead>
      <Button
        variant="ghost"
        onClick={() => handleSort(field)}
        className="h-auto p-0 font-medium hover:bg-transparent"
      >
        {children}
        {sortField === field && (
          <span className="ml-1">
            {sortDirection === 'asc' ? '‚Üë' : '‚Üì'}
          </span>
        )}
      </Button>
    </TableHead>
  );

  return (
    <div className="rounded-md border">
      <Table>
        <TableHeader>
          <TableRow>
            <SortableHeader field="name">Nombre</SortableHeader>
            <TableHead>Versi√≥n</TableHead>
            <TableHead>Estado</TableHead>
            <SortableHeader field="lastModified">√öltima Modificaci√≥n</SortableHeader>
            <SortableHeader field="owner">Propietario</SortableHeader>
            <SortableHeader field="size">Tama√±o</SortableHeader>
            <TableHead>Acciones</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {sortedDocuments.map((document) => (
            <TableRow key={document.id} className="hover:bg-gray-50">
              <TableCell>
                <div className="flex items-center gap-3">
                  <span className="text-lg">{getFileTypeIcon(document.type)}</span>
                  <div>
                    <div className="font-medium text-gray-900">{document.name}</div>
                    <div className="text-sm text-gray-500">{document.type}</div>
                  </div>
                </div>
              </TableCell>
              <TableCell>
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                  {document.version}
                </span>
              </TableCell>
              <TableCell>
                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(document.status)}`}>
                  {document.status.charAt(0).toUpperCase() + document.status.slice(1)}
                </span>
              </TableCell>
              <TableCell className="text-gray-600">
                {formatDate(document.lastModified)}
              </TableCell>
              <TableCell>
                <div className="flex items-center gap-2">
                  <div className="h-6 w-6 rounded-full bg-gray-300 flex items-center justify-center">
                    <span className="text-xs font-medium text-gray-700">
                      {document.owner.split(' ').map((n: string) => n[0]).join('')}
                    </span>
                  </div>
                  <span className="text-gray-900">{document.owner}</span>
                </div>
              </TableCell>
              <TableCell className="text-gray-600">
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="ghost" size="sm">
                      Acciones
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem>Ver</DropdownMenuItem>
                    <DropdownMenuItem>Descargar</DropdownMenuItem>
                    <DropdownMenuItem>Editar</DropdownMenuItem>
                    <DropdownMenuItem onClick={() => onViewVersionHistory(document)}>
                      Historial de Versiones
                    </DropdownMenuItem>
                    <DropdownMenuItem onClick={() => onViewApprovalHistory(document)}>
                      Historial de Aprobaciones
                    </DropdownMenuItem>
                    {canApprove && document.status === 'pendiente' && (
                      <DropdownMenuItem onClick={() => onApprovalAction(document)}>
                        Aprobar/Rechazar
                      </DropdownMenuItem>
                    )}
                    <DropdownMenuItem>Compartir</DropdownMenuItem>
                    <DropdownMenuItem className="text-red-600">Eliminar</DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      
      {sortedDocuments.length === 0 && (
        <div className="text-center py-8 text-gray-500">
          <div className="text-4xl mb-2">üìÇ</div>
          <p className="text-lg font-medium">No se encontraron documentos</p>
          <p className="text-sm">Intenta ajustar tus criterios de b√∫squeda</p>
        </div>
      )}
    </div>
  );
}
